// src/app/auth/signin/page.tsx – Login form (client-side for interactivity; base Supabase client for browser mutations – best for Next.js 15+ pivot, async-safe without deprecated helpers)
'use client';  // Logic: Client component (best for form state – no SSR overhead for inputs; push back: Server actions for mutations if scaling auth heavy)

import { createClient } from '@supabase/supabase-js';  // Base package client (no helpers – lighter, future-proof; uses NEXT_PUBLIC vars for browser)
import { useRouter } from 'next/navigation';
import { useState } from 'react';
import { Button } from '@/components/ui/button';  // Shadcn (best for consistent UI – install if missing; accessible, themed easily)
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';  // Shadcn (groups form – "terminal window" feel)
import { Input } from '@/components/ui/input';  // Shadcn (polished inputs – validation friendly)

export default function SignIn() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState<string | null>(null);
  const router = useRouter();

  // Logic: Client-side creation (best practice: Use base createClient for browser – env vars available via process.env.NEXT_PUBLIC_*; no cookies needed on client as sessions persist via localStorage/JWT)
  const supabase = createClient(
    process.env.NEXT_PUBLIC_SUPABASE_URL!,
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
  );

  const handleSignIn = async (e: React.FormEvent) => {
    e.preventDefault();
    if (password.length < 6) return setError('Password must be at least 6 characters');  // Logic: Basic validation (push back: Add more for security – e.g., regex for strength; best practice to prevent weak creds in real estate apps with sensitive leads)
    const { error } = await supabase.auth.signInWithPassword({ email, password });  // Logic: Supabase native (hashing/JWT auto – no custom bcrypt)
    if (error) setError(error.message);  // UX: Feedback for flow (e.g., "Invalid credentials" – improves rep login experience)
    else router.push('/dashboard');  // Logic: Redirect to HQ (smooth, client-side – best for game-like navigation)
  };

  return (
    <div className="flex min-h-screen items-center justify-center bg-black text-green-400 font-mono">
      <Card className="w-[350px] bg-black/80 border-green-500 shadow-[0_0_15px_rgba(0,255,0,0.7)]">  // Logic: Shadcn Card for "terminal window" (grouped, themed – enhances retro feel; push back: Add ASCII art in header for more "game" immersion)
        <CardHeader>
          <CardTitle className="text-center text-2xl">Enter Wraelen HQ</CardTitle>  // Logic: Header for clarity (best for UX – sets "game" tone)
        </CardHeader>
        <CardContent>
          {error && <p className="text-red-500 mb-4 text-center">{error}</p>}  // Logic: Conditional error (best for UX – clear, non-blocking)
          <Input
            type="email"
            placeholder="Email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="mb-4 bg-black border-green-500 text-green-400 placeholder-green-600 focus:border-green-300"
          />  // Logic: Shadcn Input (polished, responsive – better than raw for mobile reps)
          <Input
            type="password"
            placeholder="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="mb-6 bg-black border-green-500 text-green-400 placeholder-green-600 focus:border-green-300"
          />
          <Button type="submit" className="w-full bg-green-500 text-black hover:bg-green-600">Login</Button>  // Logic: Shadcn Button (themed, hover effects – "game" button feel)
        </CardContent>
      </Card>
    </div>
  );
}